* {
  box-sizing: border-box;
}

*:before,
*:after {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  position: relative;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html,
body,
#app,
#app > div,
.mynav {
  width: 100%;
}

nav {
  z-index: 1;
}

html {
  overflow-y: scroll;
}

.container {
  position: relative;
  overflow: hidden;
  display: block;
  padding-bottom: 100px;
  top: 64px;
  min-height: 90vh;
  padding-bottom: 60px;
}

.main-container {
  position: relative;
  top: 106px;
}

.projects-container {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  min-height: 100vh;
  overflow: hidden;
}

.top-container {
  margin-top: 40px;
  height: 90%;
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
}

.footer-element {
  float: left;
  width: 33%;
  height: 50px;
  line-height: 50px;
  text-align: center;
}

#reactLogo {
  height: 50px;
  width: 50px;
}

.account_links {
  margin-left: 3px;
}

.accounts {
  height: 22px;
  width: 22px;
  border-radius: 50%;
}

.project-image {
  float: left;
  width: 30%;
}

.project-box {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  width: 100%;
  min-height: 10vh;
  overflow: hidden;
}

.project-subbox {
  float: left;
  width: 70%;
}

/* Desktops and laptops ----------- */
@media only screen and (min-width: 990px) {
  .project-image {
    width: 207px;
  }
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

